footer{
    width: 100%;
    height: 300px;
    background-color: #000;
}

.f_content{
    display: flex;
    height: 300px;
    justify-content: space-between;
    align-items: center;
}


.f_content h2{
    color: #fff;
    font-size: 36px;
    font-weight: 500;
}


.btn_l{
    margin-right: 40px; 
}

@media (max-width: 480px) {
    .f_content{
        flex-direction: column;
    }

    .f_imgs{
        margin-bottom: 20px;
    }

    .f_content h2{
        font-size: 30px;
    }
    
}

