.Test{
    margin-top: 100px;
}

.Test h2{
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 30px;
}

.test_btns{
    display: flex;
}

.test_btns a{
    color: #222;
    font-size: 20px;
    font-weight: 500;
    margin-right: 30px;
    cursor: pointer;
}

.test_btns a::after{
    display: block;
    content: '';
    width: 100%;
    height: 1px;
    margin-top: 5px;
    background-color: #222;
    opacity: 0;
    transition: all ease-in-out .5s;
}



.test_btns a:hover::after{
    opacity: 1;
}

.test_btns a.test_btns_active::after{
    opacity: 1;
}

@media (max-width: 487px) {
    .Test h2{
        font-size: 30px;
    }
}





