.opis_content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 50px;
}

.opis_item h2{
    color: #000;
    font-weight: 700;
    font-size: 36px;
}

.opis_item img{
    width: 100%;
}

.opis_item p{
    font-size: 16px;
    font-weight: 300;
    color: #222222;
    margin-top: 20px;
}


@media (max-width: 990px) {
    .opis_content{
        flex-wrap: wrap;
        justify-content: center;
    }

    .opis_item{
        margin-bottom: 20px;
    }

    
}

@media (max-width: 487px) {
    .opis_item h2{
        font-size: 30px;
    }
}