.prem_content{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 50px;
}

.prem_item{
    width: 50%;
}

.item_b img{
    width: 100%;
}

.prem_one{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    width: 100%;
}

.prem_block{
    text-align: center;
    width: 170px;

}

.prem_btn a{
    padding: 7px 15px;
    border: 1px solid #000;
    color: #000;
    transition: all ease-in-out .5s;
}

.prem_btn a:hover{
    background-color: #000;
    color: #fff;
}

.prem_btn{
    width: 100%;
    text-align: center;
    margin-top: 50px;
}

@media (max-width: 1080px) {
    .prem_content{
        flex-wrap: wrap;
    }

    .prem_item{
        width: 100%;
        text-align: center;
        margin-top: 20px;
    }

    .item_b{
        order: 1;
    }
    
}

@media (max-width: 487px) {
    .prem_block p{
        font-size: 14px;
    }
}