.hamburger-menu{
    display: none;
    position: fixed;
    z-index: 11;
}


#menu__toggle {
    opacity: 0;
  }
  #menu__toggle:checked + .menu__btn > span {
    transform: rotate(45deg);
  }
  #menu__toggle:checked + .menu__btn > span::before {
    top: 0;
    transform: rotate(0deg);
  }
  #menu__toggle:checked + .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__box {
    right: 0 !important;
  }
  .menu__btn {
    position: fixed;
    top: 40px;
    z-index: 1111;
    right: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
    transition-duration: .25s;
    
  }
  .menu__btn > span::before {
    content: '';
    top: -8px;
  }
  .menu__btn > span::after {
    content: '';
    top: 8px;
  }
  .menu__box {
    display: block;
    position: fixed;
    top: 80px;
    right: -100%;
    width: 50%;
    height: 100vh;
    margin: 0;
    padding: 20px 0;
    list-style: none;
    background-color: rgba(34, 34, 34, 0.65);
    backdrop-filter: blur(5px);
    box-shadow: 2px 2px 6px rgba(0, 0, 0, .4);
    transition-duration: .25s;
  }
  .menu__item {
    display: block;
    padding: 12px 24px;
    color: #fff;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 600;
    text-decoration: none;
    transition-duration: .25s;

  }
  .menu__item:hover {
    background-color: rgb(29, 29, 29);
  }

  .menu_imgs{
    text-align: center;
    margin-top: 15px;
  }

  .img{
    margin-right: 20px;
  }

  @media (max-width: 470px) {
    .hamburger-menu{
        display: block;
    }
  }