nav {
    height: 80px;
    z-index: 1;
    top: 0;
    width: 100%;
    position: fixed;
    background-color: rgba(34, 34, 34, 0.65);
    backdrop-filter: blur(5px);
}

.nav_c {

    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

nav h2 {
    color: #fff;
    font-family: 'Merriweather', serif;
    font-weight: bold;
    font-size: 24px;
}

.nav_btns {
    display: flex;
}

.nav_btns a {
    color: #fff;
    margin-left: 50px;
    font-weight: 300;
    font-size: 16px;
}

.nav_btns a::after {
    display: block;
    content: '';
    width: 100%;
    height: 1px;
    margin-top: 5px;
    background-color: #fff;
    opacity: 0;
    transition: all ease-in-out .5s;
}

.nav_btns a:hover::after {
    opacity: 1;
}



@media (max-width: 470px) {
    .nav_btns {
        display: none;
    }
}