.header{
    width: 100%;
    height: 100vh;
    background: url('../../img/Main.jpg') no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;

}

.header_content{
    padding-top: 170px;
}

.header_content h1{
    color: #fff;
    font-size: 36px;
    font-weight: 500;
}

.header_content h1 b{
    font-weight: 700;
}

.header_content p{
    margin-top: 20px;
    font-size: 20px;
    font-weight: 300;
    color: #F1ECEC;
    margin-bottom: 30px;
}

.header_content a{
    padding: 7px 15px;
    color: #fff;
    border: 1px solid #fff;
    transition: all ease-in-out .5s;
}

.header_content a:hover{
    background-color: #fff;
    color: #222222;
}

.down{
    position: absolute;
    bottom: 30px;
    left: 50%;
}

@media (max-width: 820px) {
    .header{
        height: 80vh;
    }
}

@media (max-width: 700px) {
    .header{
        height: 70vh;
    }
}

@media (max-width: 500px) {
    .header{
        height: 70vh;
    }

    .header_content{
        padding-top: 120px;
    }
}

@media (max-width: 487px) {
    .header_content p{
        font-size: 16px;
    }

    

    .header_content h1{
        font-size: 30px;
    }
    
}