.test_main{
    height: auto;
    background-color: #000;
    margin-top: 10px;
}

.test_main p{
    color: #F1ECEC;
    width: 100%;
    max-width: 829px;
    padding-top: 50px;
    padding-bottom: 50px;
    font-size: 16px;
    font-weight: 300;
    animation: p ease-in-out .2s;
}

@keyframes p {
    0%{
        position: relative;
        top: 0;
    }

    50%{
        position: relative;
        top: -10px;
    }

    100%{
        position: relative;
        top: 0px;
    }
}
.test_pokaz{
    text-align: left;
}

.pokaz_line{
    width: 100%;
    max-width: 404px;
    height: 20px;
    background-color: #fff;
}

.line_active{
    height: 20px;
    background-color: #2142B6;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #fff;
    /* animation: Active_line ease-in .5s; */
}


.line_active h4{
    margin-right: 10px;
    font-size: 16px;
    font-weight: 300;
}

.line_active_one{
    animation: Active_line_one ease-in-out .5s;
}

@keyframes Active_line_one {
    0%{
        width: 10%;
    }

    100%{
        width: 50%;
    }
}

.line_active_two{
    animation: Active_line_two ease-in-out .6s;
}

@keyframes Active_line_two{
    0%{
        width: 30%;
    }

    100%{
        width: 40%;
    }
}

.line_active_three{
    animation: Active_line_three ease-in-out .7s;
}

@keyframes Active_line_three {
    0%{
        width: 20%;
    }

    100%{
        width: 30%;
    }
}

.line_active_four{
    animation: Active_line_four ease-in-out .8s;
}

@keyframes Active_line_four {
    0%{
        width: 10%;
    }

    100%{
        width: 40%;
    }
}

.line_active_five{
    animation: Active_line_five ease-in-out .9s;
}

@keyframes Active_line_five {
    0%{
        width: 20%;
    }

    100%{
        width: 80%;
    }
}

.line_active_six{
    animation: Active_line_six ease-in-out 1s;
}

@keyframes Active_line_six {
    0%{
        width: 20%;
    }

    100%{
        width: 60%;
    }
}

.pokaz_one{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 30px;
}

.pokaz_one h3{
    color: #fff;
    font-weight: 500;
    font-size: 20px;
    
}

.test_pokaz{
    display: flex;
    align-items: center;

}

.pokaz_item{
    width: 50%;
}

.pokaz_item_m{
    margin-left: 120px;
}

.stats{
    width: 100%;
    margin-top: 80px;
}


.stats_pokazat{
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.stats_line{
    
    width: 10%;
    text-align: center;
}

.stats_line div{
    margin: 0 auto;
    width: 10px;
    height: 30px;
    background-color: #2142B6;
}

.stats_line h4{
    color: #fff;
    margin-bottom: 30px;
    font-weight: 300;
    margin-top: 30px;

}


.stats_drone{
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    position: relative;
}

.drone_line{
    width: 20px;
    height: 5px;
    background-color: #fff;
    margin-right: 20px;
}

.drone{
    width: 50%;
    background-color: #000;
    animation: drone ease 1s;
    position: absolute;
    left: 22%;
}

@keyframes drone {

    0%{
        left: 0;
    }

    100%{
        left: 22%;

    }
    
}


@media (max-width: 1240px){
    .test_pokaz{
        flex-wrap: wrap;
        justify-content: center;
    }

    .pokaz_item_m{
        margin-left: 0;
    }

    .pokaz_item{
        width: 60%;
        margin-top: 20px;
    }
}


@media (max-width: 945px){
    .pokaz_item{
        width: 80%;
    }
}

@media (max-width: 715px){
    .pokaz_item{
        width: 100%;
    }
}

@media (max-width: 660px){
    .stats{
        display: none;
    }
}

@media (max-width: 580px) {
    .pokaz_line{
        max-width: 350px;
    }
}

@media (max-width: 487px) {
    .test_main p{
        font-size: 14px;
    }

    .pokaz_one h3{
        font-size: 16px;
    }

    .pokaz_line{
        max-width: 280px;
    }

    .line_active h4{
        font-size: 14px;
    }
}

@media (max-width: 440px) {
    .pokaz_line{
        max-width: 250px;
    }
}

@media (max-width: 410px) {
    .pokaz_line{
        max-width: 230px;
    }
}

